/*------------------------------------*\
    # KEY POINTS MODIFICATIONS \\DARIO//
\*------------------------------------*/
.lx-keypoints-component-wrap {
    border-top: 5px solid #cb0101;
    border-left: 1px solid #ededed;
    padding: 8px;
    border-right: 1px solid #ededed;
    border-bottom: 3px solid #ededed;
}



/*------------------------------------*\
    # KEY POINTS
\*------------------------------------*/

.lx-c-key-points {
    list-style: none;
}

.lx-c-key-points__item {
    position: relative;

    margin-bottom: $gel-alt-spacing-unit;

    &:before {
        position: absolute;
        top: 18px;
        left: 8px;
        bottom: -10px;

        width: 1px;

        background-color: $sp-c-silver;

        content: '';
    }


    &:last-child {
        &:before {
            display: none;
        }
    }
}

.lx-c-key-points__link {
    color: $sp-c-black;
    text-decoration: none;
}

.lx-c-key-points__icon {
    width: double($gel-spacing-unit);
    height: double($gel-spacing-unit);

    @include theme('sport') {
        color: $sp-c-live-blue;
    }
    @include theme('news') {
        color: $nw-c-red;
    }
}

.lx-c-key-points__timestamp {
    display: block;
    margin-bottom: halve($gel-spacing-unit);

    color: $sp-c-mortar;
}

.lx-c-key-points__text {
    .lx-c-key-points__link:focus &,
    .lx-c-key-points__link:hover & {
        @include theme('sport') {
            color: $sp-c-dark-blue;
        }
        @include theme('news') {
            color: $nw-c-dark-blue;
        }
    }
}

