///*------------------------------------*\
//    # CURIUM USED FOR TOP STORIES
//\*------------------------------------*/

.curium {
  @include flex-display;
  @include flex(1, 1, 100%);

  .curium__body {
    overflow: hidden;

      @include flex(1);
      @include flex-display;
      @include flex-direction(column);
      @include flex-justify(space-between);

      @include mq($until:gel-bp-m) {
        .curium--has-media & {
          width: gel-columns(7/12);
        }
      }
      @include mq($from:gel-bp-m) {
        width: 100%;
        min-height: 66px;
      }
  }


/**
* The reverse variant of 'curium' By default a
* stacked layout with the image above the content,
* from medium (600px) it is all inline.
*/
  .curium--reverse {
    @include flex-direction(column);

    @include mq($from:gel-bp-m) {
      @include flex-direction(row);
    }
    .curium__media {
      width: 100%;
      margin-bottom: $gel-spacing-unit;

      @include mq($from:gel-bp-m) {
        margin-bottom: 0;
      }
    }
    .curium__body {
      width: 100%;
      min-height: initial;

        @include mq($from:gel-bp-m) {
          padding-top: $gel-spacing-unit;

        }
    }
  }

}


///*------------------------------------*\
//    # RIVER DISPLAYS HEADLINES ONLY
//\*------------------------------------*/

.curium__river {

  .curium__meta {
      margin-top: $gel-spacing-unit;

      @include mq($from:gel-bp-m) {
        margin-top: $gel-spacing-unit;
      }
    }

  .curium__item--river{
    padding-top: $gel-spacing-unit;
    padding-bottom: $gel-spacing-unit;
  }

  @include mq($from:gel-bp-m, $until:gel-bp-l) {
    .curium__item--river {
      margin-bottom: 0;

      .curium__media {
        display: none;
      }
    }

    .curium__item--6,
    .curium__item--9,
    .curium__item--12,
     {
      &:before {
        display: none;
      }
       &:after {
        left: double($gel-spacing-unit) !important;
      }
    }

    .curium__item--6,
    .curium__item--7,
    .curium__item--8,
    .curium__item--9,
    .curium__item--10,
    .curium__item--11,
    .curium__item--12,
    .curium__item--13 {
      &:before {
        top: $gel-alt-spacing-unit;
        bottom: $gel-alt-spacing-unit;
      }
      &:after {
        left: 0px;
      }
    }
  }

    @include mq($from:gel-bp-l) {
    .curium__item--river {
      padding-bottom:$gel-alt-spacing-unit;
      padding-top: $gel-alt-spacing-unit;

      .curium__media {
        display: none;
      }
      }
      .curium__item--6{
        padding-top: 0;

         &:before {
          display: none;
          top: 0;
          bottom: 0;
        }
        &:after {
          display: none;
        }
      }
      .curium__item--7,
      .curium__item--8,
      .curium__item--9,
      .curium__item--10,
      .curium__item--11,
      .curium__item--12,
      .curium__item--13 {
        &:before {
          top: 0;
          bottom: 0;
           display: none;
        }
        &:after {
          left: double($gel-spacing-unit) !important;
        }
      }
    }

  @include mq($from:gel-bp-xxl) {


    .curium__item--6,
    .curium__item--7,
    .curium__item--8,
    .curium__item--9,
    .curium__item--10,
    .curium__item--11,
    .curium__item--12,
    .curium__item--13 {
      &:before {
        top: 0;
        bottom: 0;
      }
      &:after {
        left: double($gel-alt-spacing-unit) !important;
      }
    }

  }


}

.curium__summary{
  color: $summary-copy;
}

.curium__meta {
  margin-top: $gel-spacing-unit;

  @include mq($from:gel-bp-m) {
    margin-top: double($gel-spacing-unit);
  }
}

.curium__item {

    position: relative;
    display: inline-block;
    width: 100%;
    border-top: 0;
    vertical-align: top;

    &:before {
      position: absolute;
      right: 0;
      top: 0;
      left: $gel-spacing-unit;
      display: block;
      border: 0 solid $c-silver;
      border-top-width: 1px;
      content: '';
    }

    &.anfield__item--1 {
      &:before {
        display: none;
      }
    }

    .curium__item--headline {
      min-height: 74px;
    }



  @include mq($from:gel-bp-s) {
    padding-top: $gel-alt-spacing-unit;
    padding-bottom: $gel-alt-spacing-unit;
  }

  @include mq($from:gel-bp-m) {
    @include flex-direction(column);
    @include flex-display;
    padding-top: $gel-spacing-unit;
    padding-bottom: $gel-spacing-unit;

    &:before {
      top: $gel-spacing-unit;
      right: auto;
      bottom: $gel-spacing-unit;
      left: $gel-spacing-unit;
      border-top-width: 0;
      border-left-width: 1px;
      content: '';
    }
    &:after {
      position: absolute;
      right: 0;
      top: 0;
      left: 0;
      display: block;
      border: 0 solid $c-silver;
      border-top-width: 1px;
      content: '';
    }
    &.curium__item--1 {
      padding-top: 0;

      &:after {
        display: none;
      }
      &:before {
        display: none;
      }
    }

    &.curium__item--3,
    &.curium__item--4,
    &.curium__item--5, {
      &:before {
        display: none;
      }
    }

    &.curium__item--river {
      .curium__media {
        display: none;
      }
    }
  }

  @include mq($from:gel-bp-m, $until:gel-bp-xxl) {
    &.curium__item--2 {
      padding-top: 0;
      &:after {
        display: none;
      }
      &:before {
        top: 0;
      }
    }

    &.curium__item--3 {
      &:before {
        display: none;
      }
      &:after {
        left: 16px !important;
      }
    }
  }


  @include mq($from:gel-bp-xxl) {
    padding-top: $gel-alt-spacing-unit;
    padding-bottom: $gel-alt-spacing-unit;

    &:before {
      top: $gel-alt-spacing-unit;
      bottom: $gel-alt-spacing-unit;
      left: $gel-alt-spacing-unit;
    }
    &.curium__item--1 {
      &:after {
        display: none;
      }
    }
    &.curium__item--2 {

      &:before {
        display: none;
      }
      &:after {
        display: block;
        left: 24px !important;
      }
    }
    &.curium__item--15 {
      &:before {
        bottom: 0;
      }
      &:after {
        left: 0 !important;
      }
    }

  }
}


///*------------------------------------*\
//    # LEAD PROMO
//\*------------------------------------*/

.curium--lead {
  @include mq($from:gel-bp-m) {
    @include flex-direction(column);

    .curium__media {
      width: gel-columns(12/1);
    }
    .curium__body {
      width: gel-columns(12/1);
    }
  }


  @include mq($from:gel-bp-xxl) {
     @include flex-display;
    @include flex-direction(row);
    @include flex(1, 1, 100%);

    .curium__media {
      width: gel-columns(12/24);
      order: 2;
      padding-left: $gel-alt-spacing-unit;
    }
    .curium__body {
      width: gel-columns(12/24);
      float: right;
      padding-right: $gel-alt-spacing-unit;
    }
  }
}

///*------------------------------------*\
//    # INDEX ALSOS
//\*------------------------------------*/

.index-alsos__item{
  padding-top: $gel-alt-spacing-unit;
}



///*------------------------------------*\
//    # LEAD PROMO OVERLAY TEXT
//\*------------------------------------*/

@include mq($from:gel-bp-xxl){
  .lead-promo__content-mask .curium__title{
    display: none;
  }
}
@include mq($from:gel-bp-m, $until:gel-bp-xxl){
.curium--lead .curium__body .curium__title{display:none;}
.media-island{position:relative;}
.lead-promo__content-wrapper{
  padding-left:8px;
  padding-bottom:8px;
}
.lead-promo__content-mask{
  .curium__title a{color: #fff;}
  position: absolute;
    right: 0;
    bottom: 0;
    left: 0px;
    background-image: linear-gradient(to bottom,rgba(0,17,22,0) 0,rgba(0,17,22,.85) 100%);
    background-size: contain;
}
}
